export default {
  namespaced: true,
  state: {
    currentUser: {},
    currentPermission: [],
    permissions: {
      'modify_server_info': 25,  // 
      'start_stop_server': 24,  // 
      'delete_server': 23,  // 
      'add_server': 22,  // 
      'add_project': 1,  // 
      'create_service': 32,  // 
      'delete_project': 5,  // 
      'build_project': 6,  // 
      'deploy_project': 7,  // 
      'modify_project_info': 8,  // 

      'add_image': 27,  // 
      'deploy_image': 28,  // 
      'delete_image': 29,  // 
      'delete_project_version': 30,  // 
      'update_project_version': 31,  // 

      'add_directory': 2,  // 
      'delete_file': 14,  // 
      'preview_file': 12,  // 
      'upload_file': 9,  // 
      'delete_directory': 10,  // 
      'download_file': 11,  // 
      'copy_file_url': 13,  // url

      'company_identify': 15,  // 
      'modify_company_info': 3,  // 

      'audit_employee': 17,  // 
      'set_employee_permission': 18,  // 
      'dismiss_employee': 19,  // 
      'view_employee_id_info': 16,  // 
      'invite_new_employee': 26,  // 

      'modify_permission_template': 20,  // 
      'delete_permission_template': 21,  // 
      'add_permission_template': 4  // 
    }
  },
  mutations: {
    'UPDATE' (state, data) {
      // 
      state.currentUser = data
    },
    'GET_USER' () {
      // 
    },
    'getPermission' (state, data) {
      let arry = []
      let arryData = []
      if (Array.isArray(data.permissions)) {
        data.permissions.forEach(item => {
          if (item.id) {
            arryData.push(item.id)
          } else {
            arryData.push(item)
          }
        })
        data.permissions = arryData
      }
      if (Array.isArray(data.access_servers)) {
        arryData = []
        data.access_servers.forEach(item => {
          if (item.sid) {
            arryData.push(item.sid)
          } else {
            arryData.push(item)
          }
        })
        data.access_servers = arryData
      }
      if (Array.isArray(data.access_projects)) {
        arryData = []
        data.access_projects.forEach(item => {
          if (item.id) {
            arryData.push(item.id)
          } else {
            arryData.push(item)
          }
        })
        data.access_projects = arryData
      }
      if (Array.isArray(data.access_filehub)) {
        arryData = []
        data.access_filehub.forEach(item => {
          if (item.id) {
            arryData.push(item.id)
          } else {
            arryData.push(item)
          }
        })
        data.access_filehub = arryData
      }
      arry = arry.concat(data.permissions, data.access_servers, data.access_projects, data.access_filehub)
      state.currentPermission = arry
    },
    isPermission (state, permissionName) {
      return state.currentUser.is_admin || state.user.currentPermission.indexOf(state.permissions[permissionName]) !== -1
    }
  },
  actions: {
    // isPermission (store, permissionName) {
    //   return store.rootState.user.currentUser.is_admin || store.rootState.user.currentPermission.indexOf(store.rootState.user.permissions[permissionName]) !== -1
    // }
  }
}



